@import "../common/variables.less";

.background {
  position: relative;

  &::before {
    @media (min-width: @tablet) {
      content: "";
      position: absolute;
      left: -50px;
      width: calc(100% + 100px);
      height: 100%;
      background-color: @color-secondary;
      z-index: -1;
    }
  }
}
